import React, { Component } from 'react';
import '../css/main.scss';

class Languages extends Component {
  render(){
    return (
      <div id = 'languages'>

      <p className = "category">
        <p className = "logo"><img className = 'logo' src={require('../assets/speaker.png')} /></p>
        <p className = "title black">Languages</p>
        <hr />
      </p>

      <p className = "category sub">
        <p className = "title">Native </p> <p className = "tag">French / Français</p><br />
        <p className = "title">Fluent in</p><br />
        <p className = "tag">English</p><br />
        <p className = "tag">Japanese / 日本語</p><br />
        <p className = "title">learning </p>  <p className = "tag">spanish / español</p><br />
      </p>

      </div>
    )
  }
}

export default Languages;
