import React, { Component } from 'react';
import '../css/main.scss';

class Skills extends Component {
  render(){
    return (
      <div id = 'skills'>

        <p className = "category">
          <p className = "logo"><img className = 'logo' src={require('../assets/coding.png')} /></p>
          <p className = "title black">What can I code for you ?</p>
          <hr />
        </p>

        <p className = "category sub">
          <p className = "title">Softwares (windows)</p>
          <div className = "tags">
            <p className = "tag">C++</p>
            <p className = "tag">C#</p>
            <p className = "tag">VB</p>
            <p className = "sub-title">Libraries: </p>
            <p className = "tag">Qt</p>
            <p className = "tag">SFML</p>
          </div>
        </p>

        <br />

        <p className = "category sub">
          <p className = "title">Websites</p>
          <div className = "tags">
            <p className = "tag">HTML5</p>
            <p className = "tag">CSS3</p>
            <p className = "tag">PHP (OOP)</p>
            <p className = "tag">JQUERY</p>
          </div>
          <p className = "sub-title">Frameworks: </p>
          <p className = "tag">React JS</p>
          <p className = "tag">WordPress</p>
          <br />
          <p className = "sub-title">Database: </p>
          <p className = "tag">SQL</p>
          <p className = "tag">MySQL</p>
        </p>

        <br />

        <p className = "category sub">
          <p className = "title">Mobile APPS</p>
            <p className = "tag">Java</p>
        </p>

        <br />

        <p className = "category sub">
          <p className = "title blue">How I manage/code projects ?</p>
          <div className = "tags">
            <p className = "tag">MVC</p>
            <p className = "tag">OOP</p>
            <p className = "tag">Git</p>
            <p className = "tag">GitHub</p>
            <p className = "tag">Trello</p>
          </div>
        </p>

      </div>
    )
  }
}

export default Skills;
