import React, { Component } from 'react';
import '../css/main.scss';

class Sports extends Component {
  render(){
    return (
      <div id = 'skills'>

        <p className = "category">
          <p className = "logo"><img className = 'logo' src={require('../assets/target.png')} /></p>
          <p className = "title black">Sports I do</p>
          <hr />
        </p>

        <p className = "category sub">
          <div className = "tags">
            <p className = "tag">Street Workout</p><br />
            <p className = "tag">Breakdance</p><br />
            <p className = "tag">Muay Thai (boxing)</p><br />
          </div>
        </p>

      </div>
  )
}
}

export default Sports;
